html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

ul, li {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

.d-spacebeetwen {
  display: flex;
  justify-content: space-between;
}

.j-center {
  display: flex;
  justify-content: center;
}

@font-face {
  font-family: 'Montserrat';
  src: url("../fonts/Montserrat-Medium.woff2") format("woff2"), url("../fonts/Montserrat-Medium.woff") format("woff");
  /* Pretty Modern Browsers */
  font-style: normal;
  font-weight: 500;
  font-display: swap;
}

@font-face {
  font-family: 'Montserrat';
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff");
  /* Pretty Modern Browsers */
  font-style: normal;
  font-weight: 700;
  font-display: swap;
}

@font-face {
  font-family: 'Montserrat';
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff");
  /* Pretty Modern Browsers */
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}

@font-face {
  font-family: 'Montserrat';
  src: url("../fonts/Montserrat-ExtraBold") format("woff2"), url("../fonts/Montserrat-ExtraBold.woff") format("woff");
  /* Pretty Modern Browsers */
  font-style: normal;
  font-weight: 800;
  font-display: swap;
}

.body {
  font-family: 'Montserrat';
  font-weight: 500;
  font-size: 14px;
}

.container {
  width: 1300px;
  margin: 0 auto;
  padding: 0 15px;
}

.header {
  background-image: url("../images/bg__header .jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  padding-top: 23px;
  padding-bottom: 240px;
  width: 100%;
  height: 100%;
}

.header__top {
  align-items: center;
}

.menu {
  display: flex;
}

.menu__list {
  width: 423px;
  margin-right: 29px;
}

.menu__list-item--link {
  color: #fff;
}

.header__top-lang--link {
  color: #17AADF;
  border: 1px solid #17AADF;
  border-radius: 14px;
  padding: 5px 10px;
}

.header__top-logo {
  display: block;
}

.header__top-logo--images {
  display: block;
  margin: 131px auto 0;
  height: 422.00054931640625px;
  width: 348.0055847167969px;
}

.info {
  padding-top: 60px;
}

.info__title {
  font-family: 'Montserrat';
  font-weight: 700;
  font-size: 36px;
}

.achievements__item {
  max-width: 220px;
  margin-top: 40px;
}

.achievements__item-number {
  font-family: 'Montserrat';
  font-weight: 800;
  font-size: 96px;
  color: gold;
}

@media (max-width: 720px) {
  .container--fluid {
    padding: 0 15px;
  }
}
